import React, { useEffect, useState } from 'react';
import { ethers } from 'ethers';
import { create } from 'ipfs-http-client';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

import tolLogo from './assets/images/theotherleaf.svg';
import ethLogo from './assets/images/ethereum-logo.svg';
import twitterLogo from './assets/images/twitter-logo.svg';
import MyEpicNFT from './utils/MyEpicNFT.json';
import { templateSvg } from './utils/template';
import './styles/App.css';

const TWITTER_HANDLE = 'theotherleaf';
const TWITTER_LINK = `https://twitter.com/${TWITTER_HANDLE}`;

const CONTRACT_ADDRESS = "0x03bc7F39aF6036Db9e5985CE6Fb682C1418B16e2";
const CONTRACT_ABI = MyEpicNFT.abi;
const CLIENT = create('https://ipfs.infura.io:5001/api/v0')

const App = () => {
  const [currentAccount, setCurrentAccount] = useState(null);
  const [isLoading, setIsLoading] = useState(false);
  const [contractAssets, setContractAssets] = useState([]);
  // const [pubContractAssets, setPubContractAssets] = useState([]);
  const [isMainnet, setIsMainnet] = useState(false);

  const checkIfWalletIsConnected = async () => {
    const { ethereum } = window;

    if (!ethereum) {
      toast.error("Make sure you have metamask!");
      return;
    }

    console.log("We have the ethereum object", ethereum);
    const accounts = await ethereum.request({ method: 'eth_accounts' });

    if (accounts.length === 0) {
      toast.warning("Please connect to metamask!");
      return;
    }

    const account = accounts[0];
    setCurrentAccount(account);

    const chainId = await ethereum.request({ method: 'eth_chainId' });
    if (chainId !== '0x4') {
      toast.error("Please connect to Rinkeby Testnet!");
      return;
    }

    setIsMainnet(false);
    toast.info("Connected to Rinkeby Testnet");
  };

  const connectWallet = async () => {
    try {
      const { ethereum } = window;

      if (!ethereum) {
        toast.error("Make sure you have metamask!");
        return;
      }

      const accounts = await ethereum.request({ method: "eth_requestAccounts" });
      setCurrentAccount(accounts[0]);
      toast.success("Connected");
    } catch (error) {
      console.log(error);
    }
  };

  const askContractToMintNft = async () => {  
    if (isLoading) {
      return;
    }

    try {
      const { ethereum } = window;

      if (!ethereum) {
        toast.error("Make sure you have metamask!");
        return;
      }

      setIsLoading(true);
      const provider = new ethers.providers.Web3Provider(ethereum);
      const signer = provider.getSigner();
      const connectedContract = new ethers.Contract(CONTRACT_ADDRESS, CONTRACT_ABI, signer);
      const tokenId = await connectedContract.getCurrentTokenId();

      const response = await fetch('https://api.quotable.io/random?tags=famous-quotes&maxLength=100');
      const { content, author } = await response.json();
      const svgImage = templateSvg
        .replace('{{tokenId}}', tokenId.toString())
        .replace('{{author}}', author)
        .replace('{{content}}', content);

      const metadataObject = {
        name: `Leafy NFT #${tokenId}`,
        description: content,
        image: `data:image/svg+xml;base64,${btoa(svgImage)}`,
      };

      const metadataJSON = JSON.stringify(metadataObject);
      const { path: cid } = await CLIENT.add(metadataJSON);

      /* mint the file */
      toast.info("Pending to pay gas...");
      let nftTxn = await connectedContract.makeAnEpicNFT(`ipfs://${cid}`, { gasLimit: 300000 });
      toast.info("Minting, please wait for a while...", { autoClose: 10000 });
      await nftTxn.wait();
      const etherscanUrl = isMainnet
        ? `https://etherscan.io/tx/${nftTxn.hash}`
        : `https://rinkeby.etherscan.io/tx/${nftTxn.hash}`;
      console.log("Tx:", etherscanUrl);
    } catch (error) {
      toast.error("Unexpected error");
      setIsLoading(false);
    }
  }

  const renderNotConnectedButton = () => {
    if (currentAccount) {
      return;
    }

    return (
      <button
        className="cta-button connect-wallet-button"
        onClick={connectWallet}
      >
        Connect to Wallet
      </button>
    );
  };

  const renderMintButton = () => {
    if (!currentAccount) {
      return;
    }

    return (
      <button
        className="cta-button connect-wallet-button"
        onClick={askContractToMintNft}
      >
        {isLoading ? 'Loading...' : 'Mint NFT'}
      </button>   
    );
  };

  // useEffect(() => {
  //   const getPubAssets = async () => {
  //     try {
  //       const pubResponse = await fetch(`https://api.opensea.io/api/v1/assets?order_direction=desc&asset_contract_address=${CONTRACT_ADDRESS}`)
  //       const { pubAssets } = await pubResponse.json();
  //       setPubContractAssets(Array.isArray(pubAssets) ? pubAssets.filter((asset) => asset.image_thumbnail_url) : []);
  //     } catch (error) {
  //       console.log(error);
  //     }
  //   }

  //   getPubAssets();
  // }, [])

  useEffect(() => {
    const getAssets = async () => {
      try {
        const response = await fetch(`https://testnets-api.opensea.io/api/v1/assets?order_direction=desc&asset_contract_address=${CONTRACT_ADDRESS}`)
        const { assets } = await response.json();
        setContractAssets(Array.isArray(assets) ? assets.filter((asset) => asset.image_thumbnail_url) : []);
      } catch (error) {
        console.log(error);
      }
    }

    getAssets();
  }, [])

  useEffect(() => {
    checkIfWalletIsConnected();
  }, [])

  useEffect(() => {
    let myEpicNFTContract;

    const onNewEpicNFTMinted = (from, tokenId) => {
      setIsLoading(false);
      const openseaUrl = isMainnet
        ? `https://opensea.io/assets/${CONTRACT_ADDRESS}/${tokenId.toNumber()}`
        : `https://testnets.opensea.io/assets/${CONTRACT_ADDRESS}/${tokenId.toNumber()}`;
      toast.success((
        <>
          <div>We've minted your NFT #{tokenId.toNumber()} and sent it to your wallet!</div>
          <div>It can take a max of 10 min to show up on <a target="_blank" rel="noopener noreferrer" href={openseaUrl}>OpenSea</a>.</div>
        </>
      ))
    };

    const { ethereum } = window;
    if (ethereum) {
      const provider = new ethers.providers.Web3Provider(ethereum);
      const signer = provider.getSigner();

      myEpicNFTContract = new ethers.Contract(CONTRACT_ADDRESS, CONTRACT_ABI, signer);
      myEpicNFTContract.on("NewEpicNFTMinted", onNewEpicNFTMinted);
    }

    return () => {
      if (myEpicNFTContract) {
        myEpicNFTContract.off("NewEpicNFTMinted", onNewEpicNFTMinted);
      }
    };
  // eslint-disable-next-line
  }, []);

  return (
    <div className="App">
      <div className="container">
        <div className="header-container">
          <img alt="The Other Leaf" className="theotherleaf-logo" src={tolLogo} />
          <p className="header gradient-text">NFT Collection</p>
          <p className="sub-text">
            Each unique. Each beautiful. Discover your quote of the day!
          </p>
          {renderNotConnectedButton()}
          {renderMintButton()}
        </div>
        <div className="assets-container">
          {/* {
            pubContractAssets.map((asset) => (
              <div className="asset-container" key={asset.id}>
                <a
                  target="_blank"
                  rel="noopener noreferrer"
                  href={`https://opensea.io/assets/${CONTRACT_ADDRESS}/${asset.token_id}`}
                  title={asset.name}
                >
                  <img className="asset-image" src={asset.image_thumbnail_url} alt={asset.name} />
                  <div className="asset-info">
                    <div className='asset-detail'>
                      <div className="asset-owner">
                        Owned by {asset.owner?.address?.substring(2,8)}
                      </div>
                      <div className="asset-name">{asset.name}</div>
                    </div>
                    <div className='asset-chain'>
                      Mainnet <img src={ethLogo} alt="Ethereum" />
                    </div>
                  </div>
                </a>
              </div>
            ))
          } */}
          {
            contractAssets.map((asset) => (
              <div className="asset-container" key={asset.id}>
                <a
                  target="_blank"
                  rel="noopener noreferrer"
                  href={`https://testnets.opensea.io/assets/${CONTRACT_ADDRESS}/${asset.token_id}`}
                  title={asset.name}
                >
                  <img className="asset-image" src={asset.image_thumbnail_url} alt={asset.name} />
                  <div className="asset-info">
                    <div className='asset-detail'>
                      <div className="asset-owner">
                        Owned by {asset.owner?.address?.substring(2,8)}
                      </div>
                      <div className="asset-name">{asset.name}</div>
                    </div>
                    <div className='asset-chain'>
                      Rinkeby <img src={ethLogo} alt="Ethereum" />
                    </div>
                  </div>
                </a>
              </div>
            ))
          }
        </div>
        <div className="footer-container">
          <img alt="Twitter Logo" className="twitter-logo" src={twitterLogo} />
          <a
            className="footer-text"
            href={TWITTER_LINK}
            target="_blank"
            rel="noreferrer"
          >{`@${TWITTER_HANDLE}`}</a>
        </div>
        <ToastContainer
          position='bottom-right'
          closeButton={false}
          hideProgressBar={true}
        />
      </div>
    </div>
  );
};

export default App;
